<template>
    <div @mouseleave="hideTootips" class="inviteToRoom">
        <div class="inviteLinksWrapper">
            <div class="inviteLinks">
                <label>Room link:</label>
                <b-input-group>
                    <b-form-input @mousemove="hideTootips" id="roomLinkInput" :value="roomLink"></b-form-input>
                    <b-input-group-append>
                    <b-button @click="copyRoomLink()" variant="outline-secondary">Copy</b-button>
                    </b-input-group-append>
                    <b-tooltip :show.sync="linkCopiedToolTip" target="roomLinkInput" placement="left">
                        Copied
                    </b-tooltip>
                </b-input-group>
            </div>
            <div class="inviteLinks">
                <label>Room Id:</label>
                <b-input-group>
                    <b-form-input @mousemove="hideTootips" id="roomIdInput" :value="roomId"></b-form-input>
                    <b-input-group-append>
                    <b-button @click="copyRoomId()" variant="outline-secondary">Copy</b-button>
                    </b-input-group-append>
                    <b-tooltip :show.sync="idCopiedToolTip" target="roomIdInput" placement="left">
                        Copied
                    </b-tooltip>
                </b-input-group>
            </div>
        </div>
        <b-button variant="primary" size="sm">Invite friends</b-button>
    </div>
</template>
<script>
export default {
    data:function(){
        return {
            roomId:this.$route.params.roomId,
            roomLink:window.location.origin+'/room/'+this.$route.params.roomId,
            linkCopiedToolTip:false,
            idCopiedToolTip:false,
            clearInterval:0
        }
    },
    methods:{
        copyRoomLink(){
            var input = document.getElementById('roomLinkInput');
            this.linkCopiedToolTip = true;
            this.timeOutHideToolTip();
            input.select();
            document.execCommand('copy');
        },
        copyRoomId(){
            var input = document.getElementById('roomIdInput');
            this.idCopiedToolTip = true;
            this.timeOutHideToolTip();
            input.select();
            document.execCommand('copy');
        },
        timeOutHideToolTip(){
            clearInterval(this.clearInterval);
            this.clearInterval = setTimeout(() => {
                this.hideTootips();
            }, 2000);
        },
        hideTootips(){
            this.idCopiedToolTip = false;
            this.linkCopiedToolTip = false;
        }
    }
}
</script>